
#content {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 1520px) {
  div .restaurants_list {
    display: grid;
    grid-template-columns: auto auto auto;
    width: 100%;
    align-content: flex-start;
  }
}

@media screen  and (min-width: 1900px) {
  div .restaurants_list {
    display: grid;
    grid-template-columns: auto auto auto auto;
    width: 100%;
    align-content: flex-start;
  }
}

#tagOrders{
  margin-left:49%;
}

.restaurant-images{
  width: 371px;
  height: 219px;
}

.span {
  color:rgb(233, 162, 55);
}

#restarantImg {
  width: 100%;
  height: 100%;
  object-fit: contain;
  size:auto;
  border-radius: 25px; 
  padding:10px;
}

#restaurant-name {
  width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.3rem;
  margin: auto;
  color: white;
}

#adress {
  margin-top: -15px;
  width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: rgba(255, 255, 255, 0.76);
  margin: auto;
}

div .time-info {
  float: right;
  object-fit: contain;
  justify-content: center;
  margin-top: 0px;
}

div .columns {
  margin-right: 19px;
  margin-left: 0px;
  margin-bottom: 9px;
}

div .restaurant {
  object-fit: contain;
  margin: 0%;
  margin-top: 50px;
  height:100%;
  width: auto;
}

.content-with-orders{
  width: 450px;
  height: 400px;
  border-radius: 10px;
  box-shadow: 0 0 15px -0.9px rgba(hsl(32, 85%, 45%), 0.7);
  margin-left:12px;

}

.content-without-orders{
  width: 450px;
  height: 400px;
  margin-left:12px;
 
}

#clock {
  margin-top: -10px;
  font-size: 30px;
  margin-left: 35px;
  color: white;
}

#time {
  color: rgba(255, 255, 255, 0.76);
}

div.details-restaurant {
  margin-left: 40px;
  margin-right: 20px;
  margin-bottom: 8px;
}
